html,
body {
  height: 100%;
  margin: 0;
  font-family: 'Roboto', sans-serif; }

.orange {
  color: #E55446; }

.container {
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto; }
  @media (min-width: 768px) {
    .container {
      width: 750px; } }
  @media (min-width: 992px) {
    .container {
      width: 970px; } }
  @media (min-width: 1200px) {
    .container {
      width: 1170px; } }

.form {
  margin: 0 auto; }

.form-group {
  width: 100%;
  position: relative;
  display: block;
  padding: 5px 15px; }

.form-group label {
  color: #898888;
  font-size: 14px;
  margin: 4px;
  font-family: Roboto; }

.form-group input[type="text"],
.form-group input[type="password"],
.form-group input[type="email"],
.form-group input[type="tel"] {
  width: 100%;
  height: 20px;
  padding: 4px;
  border: 1px solid #c5c5c5;
  border-radius: 4px;
  font-family: Roboto; }

.form-group button {
  width: 100%;
  background: transparent;
  color: #030418;
  font-size: 14px;
  font-family: Roboto;
  border: 1px solid #c5c5c5;
  border-radius: 4px;
  padding: 4px;
  height: 34px;
  cursor: pointer; }



