footer {
  /* position: fixed; */
  bottom: 0;
  width: 100%;
  height: 150px;
  background-color: #F0F0F0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center; }

footer .wrapper {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px; }

footer .wrapper .title {
  color: #222222;
  font-size: 16px;
  font-weight: normal;
  margin: 0; }

footer .wrapper .col {
  min-width: 200px; }

footer .wrapper .col ul {
  width: 100%;
  padding: 0;
  margin: 0 auto;
  list-style: none;
  font-size: 10px; }

footer .wrapper .col ul li a {
  text-decoration: none;
  color: #555555;
  font-size: 10px;
  line-height: 14px; }

footer .wrapper .col ul li a:hover {
  color: #E55446; }



